//Importa√ß√£o de tudo que √© usado
import React, {useEffect, useState} from 'react'
import './css/estilo.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import {Link} from 'react-router-dom'
import Container from 'react-bootstrap/Container'
import Card from 'react-bootstrap/Card'
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'
import Spinner from 'react-bootstrap/Spinner'
import NavbarComponent from './components/Navbar'
import BannerComponent from './components/BannerComponent'

const App = () =>{

  //Defini√ß√£o dos Hooks
  const [herois, setHerois] = useState([])
  const [heroiId, setHeroiId] = useState('')
  const [carregando, setCarregando] = useState(false)
  const [erroApi, setErroApi] = useState(false)

  //Carregando os super-her√≥is e definindo o t√≠tulo na inicializa√ß√£o
  useEffect(()=>{
    document.title= "Marvel HQs"
    carregaSuperherois()
  },[])

  //Busca dos dados na API
  async function carregaSuperherois(){
    setCarregando(true)
    //Atribui√ß√£o da apiKey com a chave em arquivo local
    const apiKey = process.env.REACT_APP_APIKEY_MARVEL
    let url = `https://gateway.marvel.com/v1/public/characters?${apiKey}`
    await fetch(url)
    //Busca e tratamento dos dados
    .then(response => response.json())
    .then(data =>{
      setHerois(data.data.results)
    })
    //Verifica√ß√£o se houve erros
    .catch(function(){
      setErroApi(true)
    })
    setCarregando(false)
  }

  //Fun√ß√£o para listagem do super-her√≥is no select
  function listaHerois(props){
    const arrayHerois = []
    const herois = props
    
    //Guarda os dados de cada super-her√≥i em um elemento do array para ser tratado futuramente 
    herois.map((heroi) => 
      arrayHerois.push(heroi.id + "/" + heroi.name)
    )
    return arrayHerois
  }

  return(
    <>
      <NavbarComponent/>
      <BannerComponent/>
      <Container>
        <Row className="d-flex justify-content-center">

        {carregando &&
          //Exibido se estiver carregando
          <Spinner className="mt-5" animation="border" role="status"/>
        }

        {!carregando &&
          <Col lg={6}>
          {erroApi &&
            //Exibido se houve algum erro nos dados da API
            <Card className="semBorda mb-4">
              <Card.Header className="cardCabe√ßalho">Erro</Card.Header>
              <Card.Body className="cardCorpo">
                <Card.Text>N√£o foi poss√≠vel listar os super-her√≥is, tente novamente mais tarde.</Card.Text>
              </Card.Body>
          </Card>
          }
          {!erroApi &&
            <Card className="semBorda mb-4">
            <Card.Header className="cardCabe√ßalho">Procure o seu super-her√≥i</Card.Header>
            <Card.Body className="cardCorpo">
              <Form>
                <Form.Group>
                  <Form.Label>Selecionar:</Form.Label>
                  <Form.Control className="select" as="select" size="lg" onChange={e => setHeroiId(e.target.value)} custom>
                    <option key="1" disabled selected>Selecione o her√≥i</option>

                    {
                      //Carregamento dos dados como <option> no select
                      listaHerois(herois).map(heroi => {
                        //Para cada her√≥i √© gerado um option com os respectivos valores
                        return (<option key={heroi.split("/")[0]} value={heroi.split("/")[0]}>{heroi.split("/")[1]}</option>)
                      })
                    }

                  </Form.Control>
                </Form.Group>
              </Form>
              <Row className="d-flex justify-content-center">
                {/*Passa a URL com o id do super-her√≥i a ser pesquisado*/}
                <Link to={`/Hq/${heroiId}`}>
                  <Button className="semBorda botao">Pesquisar</Button>
                </Link>
              </Row>
            </Card.Body>
          </Card>
          }
          </Col>
        }
        </Row>
      </Container>
    </>
  )

}

export default App
