import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import Hq from './Hq';
import {BrowserRouter, Switch, Route} from 'react-router-dom'

ReactDOM.render(
  <React.StrictMode>
    {/*Defini√ß√£o do Router com basename compat√≠vel para o gh-pages*/}
    <BrowserRouter basename={process.env.PUBLIC_URL}>
    {/*Implementa√ß√£o do Switch para n√£o ocorrer erro nas trocas de p√°ginas*/}
      <Switch>
    {/*Defini√ß√£o das rotas poss√≠ves, passando se necess√°rios, os par√¢metros requisitados*/}
        <Route path="/" component={App} exact/>
        <Route path="/Hq/:id" component={Hq}/>
      </Switch>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

